* {
  box-sizing: border-box;
}

.list {
  list-style: none;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #e2ec53;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.input{
  margin-top: 20px;
  border-radius: 15px;
  background-color:#e2ec53;
  font-family:inherit;
  font-weight:500;
  font-size:20px;
  line-height:1.1;
  text-align:center;
  color:#2b6fee;
  padding:6px;
  border:2px solid transparent;
  border-color: #2b6fee;
  transition:color .25s;
  transition-timing-function:cubic-bezier(.4,0,.2,1);
  /* outline:none; */
}

.country-list_item{
  display: flex;
  margin-bottom: 10px;
}

.country-list_flag{
  margin-right: 10px;
  border-radius: 90px;
}


